import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.css";

import { Switch, Route } from 'react-router-dom';

import Vaktija from "./components/Vaktija";
import Download from "./components/Download";
import Info from "./components/Info";
import Test from "./components/Test";
import NotFound from "./components/NotFound";


class App extends Component {

    render() {
        return (
                <Switch>
                    <Route exact path={process.env.PUBLIC_URL + '/'} component={Vaktija} />
                    <Route path={process.env.PUBLIC_URL + '/download'} component={Download} />
                    <Route path={process.env.PUBLIC_URL + '/info'} component={Info} />
                    <Route path={process.env.PUBLIC_URL + '/test'} render={()=><Test test1="1" test2={2}/>}/>
                    {/* <Route path="*" component={NotFound}/> */}

                </Switch>
        );
    }
}

export default App;


